import { cloneElement } from "react"
import { HelmetProvider } from "react-helmet-async"
import { ClerkProvider } from "@clerk/clerk-react"
import { environment } from "@rp/common/environment"
import { axiosClient } from "@rp/react/axios-client"
import { CqsByIdsProvider } from "@rp/react/tanstack-api/cqs-by-ids"
import { bigDataTheme } from "@rp/react/theme"
import { ThemeProvider, clerkTheme } from "@rp/react/web/theme"
import { BigdataQueryClientProvider } from "../BigdataQueryClientProvider"
import { LaunchDarklyProvider } from "../LaunchDarklyProvider/LaunchDarklyProvider"
import { ModalProvider } from "../ModalContext"
import { ShareProvider } from "../ShareContext"
import { SnackbarProvider } from "../Snackbar/SnackbarProvider"
import { WebAppFilesProvider } from "../WebAppFilesProvider"

const {
  clerk: { clerkPubKey },
} = environment

// Add all Providers to this array
const providers = [
  <ShareProvider />,
  <HelmetProvider />,
  <BigdataQueryClientProvider children />,
  <ClerkProvider publishableKey={clerkPubKey} appearance={clerkTheme} children />,
  <ThemeProvider children theme={bigDataTheme} />,
  <ModalProvider />,
  <SnackbarProvider children />,
  <LaunchDarklyProvider children clientSideID={environment.launchDarkly.webKey} />,
]

const applicationProviders = [
  <WebAppFilesProvider children />,
  <CqsByIdsProvider client={axiosClient} />,
]

const ProviderComposer = ({ children }: { children: React.ReactNode }) =>
  providers.reduceRight((kids, parent) => cloneElement(parent, { children: kids }), children)

const ApplicationProviders = ({ children }: { children: React.ReactNode }) =>
  applicationProviders.reduceRight(
    (kids, parent) => cloneElement(parent, { children: kids }),
    children,
  )

export { ApplicationProviders, ProviderComposer }
